import {
	ProductContainer,
	ConsultForm,
	AnswersList,
	InsuranceCompanies,
} from 'components';
import cardItems from 'shared/utils/constants/cardItems';
import { answersMain } from 'shared/store/answersMain';
import styles from './Main.module.scss';

function Main() {
	return (
		<>
			<main className={styles.root}>
				<ProductContainer data={cardItems} mode="default" />
				<InsuranceCompanies
					title={' '}
					description={
						'         .      .'
					}
					nameButton={''}
					modeButton={'more'}
					disabled={false}
				/>
				<ConsultForm />
				<InsuranceCompanies
					title={'      ?'}
					description={
						'         ,     .   2           email.'
					}
					nameButton={' '}
					modeButton={'submit-request'}
					disabled={false}
				/>
				<AnswersList
					title="  "
					data={answersMain}
					mode="default"
				/>
			</main>
		</>
	);
}

export default Main;
